.layered-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
  -webkit-transition: .3s all ease .3s;
          transition: .3s all ease .3s;
}
.layered-content .floating-close {
  position: absolute;
