  margin-right: 50px;
}
.timeline.single.rtl .frame .timeline-bubble {
  margin: 0 50px 0 10px;
}
.timeline.single.rtl .frame .timeline-bubble:after {
  left: auto;
  right: -10px;
  border-right: 0;
  border-left: 10px solid #f0f0f0;
}
.timeline.single.rtl .frame .timeline-bubble:before {
  left: auto;
  right: -21px;
  border-right-color: transparent;
  border-left-color: #f0f0f0;
}
.timeline.single.rtl:before {
  left: auto;
  right: 20px;
}
.timeline.vertical {
  width: 135%;
  overflow: hidden;
  overflow-x: scroll;
  white-space: nowrap;
  padding-top: 10px;
}
.timeline.vertical .frame {
  display: inline-block;
  float: none;
  width: auto;
  top: auto;
  margin: 30px 20px 10px 0;
}
.timeline.vertical .frame .timeline-badge {
  left: auto;
  top: -51px;
  right: 50%;
  margin-right: -12px;
}
.timeline.vertical .frame .timeline-bubble {
  margin: 0;
}
.timeline.vertical .frame:nth-child(2n) {
  float: left;
}
.timeline.vertical:before {
  width: 100%;
  height: 3px;
  top: 10px;
  left: 0;
}
.timeline:before,
.timeline:after {
  content: "";
  display: table;
  clear: both;
}
.timeline:before {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 1px;
  margin-left: -1px;
  background-color: #eee;
  content: ' ';
}
.timeline .widget {
  margin: 0;
}
